body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
	font-family: 'bariol_regular';
	src: url(/Todo/static/media/bariol_regular-webfont.c0df7400.eot),
			 url(/Todo/static/media/bariol_regular-webfont.ceabb18a.woff) format('woff'),
			 url(/Todo/static/media/bariol_regular-webfont.90c21e32.woff2) format('woff2'),
			 url(/Todo/static/media/bariol_regular-webfont.ac437ca8.ttf) format('truetype');;
	font-weight: normal;
	font-style: normal;
}

*{
  box-sizing: border-box;
	font-family: 'bariol_regular';
}

body {
  background: rgb(244, 243, 243);
}

.wrapper {
  max-width: 480px;
  margin: auto;
  background: #fff;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  padding: 50px;
}

.title {
  align-self: flex-start;
  font-size: 2rem;
  font-weight: 300;
  color: #333;
  margin-bottom: 30px;
}

.tabs button{  
  margin-left: 10px;
  margin-bottom:5px;
  background:red;
}

.todoInput {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
}

input {
  width: 75%;
  margin-right: 25px;
  padding: 15px 0 15px 15px;
  border-radius: 4px;
  border: 1px solid rgb(238, 238, 238);
}

button {
  background: #62c7ff;
  border: none;
  font-size: 1rem;
  font-weight: 300;
  padding: 15px 25px;
  border-radius: 4px;
  color: #fff;
}

ul {
  padding-left: 0;
  list-style: none;
  margin-bottom: 50px;
  text-align: center;
}

li {
  font-weight: 300;
  padding: 20px 0 20px 0;
  border-top: 1px solid #eee;
  display: flex;
  justify-content: space-between;
}

li > span{
  width: 240px;
  word-wrap: break-word;
}

img {
  height: 25px;
}

button.action {
  color: #333;
  background: none;
  padding: 0;
}

button.action:focus {
  outline: none;
}

